// -------------------- KMB --------------------
export function getKMBRouteList (){
    // check if timestamp is more than {param: 7} days before system date
    let local_KMBRouteList_timestamp = localStorage.getItem("KMBRouteList_timestamp");
    let local_KMBRouteList = localStorage.getItem("KMBRouteList");

    if (local_KMBRouteList_timestamp && local_KMBRouteList){
        let days = DAYS_TO_RETRIEVE_KMB_ROUTE_LIST;
        let currDatetime = new Date();
        let day_diff = DayDifference(local_KMBRouteList_timestamp, currDatetime);
        if (day_diff < days){
            console.log("No need to download KMBRouteList");
            return new Promise((resolve) => {
                resolve("Success without download!");
            })
        }
        else {
            console.log("KMBRouteList expired and will be updated.");
        }
    }
    
    return new Promise((resolve)=>{

        axios.get(KMB_ROUTE_LIST_LINK)
        .then((response) => {
            let res_data = response.data;
            let timestamp = res_data.generated_timestamp;
            let data = JSON.stringify(res_data.data);
    
            localStorage.setItem("KMBRouteList_timestamp", timestamp);
            localStorage.setItem("KMBRouteList", data);
            
            resolve("Success!")
        })
    });
}

