// -------------------- NWFB / CTB --------------------
export function getNWFBCTBCompany (company){
    let local_company_timestamp, local_company;
    let link = CTB_COMPANY_LINK.replace("{company}", company);

    if (company == "NWFB"){
        local_company_timestamp = localStorage.getItem("NWFBCompany_timestamp");
        local_company = localStorage.getItem("NWFBCompany");
    }
    else if (company == "CTB"){
        local_company_timestamp = localStorage.getItem("CTBCompany_timestamp");
        local_company = localStorage.getItem("CTBCompany");
    }
    else {
        return new Promise((resolve) => {
            resolve("Wrong company ID");
        });
    }

    if (local_company_timestamp && local_company){
        let days = DAYS_TO_RETRIEVE_NWFBCTB_COMPANY;
        let currDatetime = new Date();
        let day_diff = DayDifference(local_company_timestamp, currDatetime);
        if (day_diff < days){
            console.log("No need to download NWFB/CTB Company");
            return new Promise((resolve) => {
                resolve("Success without download!");
            })
        }
    }

    return new Promise((resolve)=>{

        axios.get(link)
        .then((response) => {
            let res_data = response.data;
            let timestamp = res_data.generated_timestamp;
            let data = JSON.stringify(res_data.data);

            if (company == "NWFB"){
                localStorage.setItem("NWFBCompany_timestamp", timestamp);
                localStorage.setItem("NWFBCompany", data);
            }
            else if (company == "CTB"){
                localStorage.setItem("CTBCompany_timestamp", timestamp);
                localStorage.setItem("CTBCompany", data);
            }
            resolve("Success!")
        })
    });
}   

