export function getNWFBCTBRouteStopList(){
    // NWFBRouteStopList_timestamp
    // CTBRouteStopList_timestamp
    // NWFBCTBStopList_timestamp

    let local_NWFBRouteStopList_timestamp = localStorage.getItem("NWFBRouteStopList_timestamp");
    let local_NWFBRouteStopList = localStorage.getItem("NWFBRouteStopList");
    let local_CTBRouteStopList_timestamp = localStorage.getItem("CTBRouteStopList_timestamp");
    let local_CTBRouteStopList = localStorage.getItem("CTBRouteStopList");
    let local_NWFBCTBStopList_timestamp = localStorage.getItem("NWFBCTBStopList_timestamp");
    let local_NWFBCTBStopList = localStorage.getItem("NWFBCTBStopList"); 

    if (local_NWFBRouteStopList_timestamp && local_NWFBRouteStopList &&
        local_CTBRouteStopList_timestamp && local_CTBRouteStopList &&
        local_NWFBCTBStopList_timestamp && local_NWFBCTBStopList){
        let days = DAYS_TO_RETRIEVE_NWFBCTB_STOP_LIST;
        let currDatetime = new Date();
        let day_diff_1 = DayDifference(local_NWFBRouteStopList_timestamp, currDatetime);
        let day_diff_2 = DayDifference(local_CTBRouteStopList_timestamp, currDatetime);
        let day_diff_3 = DayDifference(local_NWFBCTBStopList_timestamp, currDatetime);
        if (day_diff_1 < days || day_diff_2 < days || day_diff_3 < days){
            console.log("No need to download NWFBRouteStopList / CTBRouteStopList / NWFBCTBStopList");
            return new Promise((resolve) => {
                resolve("Success without download!");
            })
        }
    }

    //////////////////////////////////////////////////////////////////////

    console.log("Start time: " + new Date());
    // retrieve RouteList, then foreach route and direction, retrieve RouteStop
    (async () => {
        
        let promises = [];

        let NWFBRouteList = JSON.parse(localStorage.getItem("NWFBRouteList"));
        let NWFBRouteStopList = [];
        let CTBRouteList = JSON.parse(localStorage.getItem("CTBRouteList"));
        let CTBRouteStopList = [];
        let StopIdList = [];
        
        // API
        console.log("[NWFB/CTB RouteStopList] Start time: " + new Date());
        for (let i = 0; i < NWFBRouteList.length; i++){
            let company = 'NWFB';
            let route = NWFBRouteList[i].route;
            let promise = getNWFBCTBRouteStop(company, route, 'outbound');
            promises.push(promise);
            promise = getNWFBCTBRouteStop(company, route, 'inbound');
            promises.push(promise);
        }
        for (let i = 0; i < CTBRouteList.length; i++){
            let company = 'CTB';
            let route = CTBRouteList[i].route;
            let promise = getNWFBCTBRouteStop(company, route, 'outbound');
            promises.push(promise);
            promise = getNWFBCTBRouteStop(company, route, 'inbound');
            promises.push(promise);
        }

        await Promise.all(promises).then((values) => {
            values.forEach((route) => {
                route = route.data;
                if (route){
                    route.forEach((routeStop) => {
                        if (!StopIdList.includes(routeStop.stop)){
                            StopIdList.push(routeStop.stop);
                        }
                        delete routeStop.data_timestamp;
                        if (routeStop.co == "NWFB"){
                            delete routeStop.co;
                            NWFBRouteStopList.push(routeStop);
                        }
                        else if (routeStop.co == "CTB"){
                            delete routeStop.co;
                            CTBRouteStopList.push(routeStop);
                        }
                    });
                }
            });
        });

        // console.log(JSON.stringify(NWFBRouteStopList));
        // console.log(JSON.stringify(CTBRouteStopList));
        // console.log(StopIdList);
        localStorage.setItem("NWFBRouteStopList", JSON.stringify(NWFBRouteStopList));
        localStorage.setItem("NWFBRouteStopList_timestamp", new Date());
        localStorage.setItem("CTBRouteStopList", JSON.stringify(CTBRouteStopList));
        localStorage.setItem("CTBRouteStopList_timestamp", new Date());
        console.log("[NWFB/CTB RouteStopList] End time: " + new Date());

        // download Stop
        let StopList = [];
        console.log("[NWFB/CTB StopList] Start time: " + new Date());
        promises = [];
        StopIdList.forEach((stopId) => {
            promises.push(getNWFBCTBStop(stopId));
        });
        await Promise.all(promises).then((values) => {
            values.forEach((stop) => {
                stop = stop.data;
                delete stop.data_timestamp;
                StopList.push(stop);
            });
        });
        // console.log(StopList);
        localStorage.setItem("NWFBCTBStopList", JSON.stringify(StopList));
        localStorage.setItem("NWFBCTBStopList_timestamp", new Date());
        console.log("[NWFB/CTB StopList] End time: " + new Date());

        return new Promise((resolve) => {
            resolve("Successful getNWFBCTBRouteStopList!");
        });

    })();
    
}

